const categories = ['All', 'Research', 'Community Service', 'Volunteering', 'Education', 'Advocacy', 'Innovation']
const timeCommitments = ['All', 'Weekend', 'Summer', 'Ongoing']
const difficulties = ['All', 'Beginner', 'Intermediate', 'Advanced']
const impacts = ['All', 'High', 'Medium', 'Low']

export default function ExtracurricularIdeasPage() {
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedCategory, setSelectedCategory] = useState('All')
  const [selectedTime, setSelectedTime] = useState('All')
  const [selectedDifficulty, setSelectedDifficulty] = useState('All')
  const [selectedImpact, setSelectedImpact] = useState('All')

  const filteredIdeas = extracurricularIdeas.filter(idea => {
    const matchesSearch = idea.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         idea.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         idea.fields.some(field => field.toLowerCase().includes(searchTerm.toLowerCase()))
    const matchesCategory = selectedCategory === 'All' || idea.category === selectedCategory
    const matchesTime = selectedTime === 'All' || idea.timeCommitment === selectedTime
    const matchesDifficulty = selectedDifficulty === 'All' || idea.difficulty === selectedDifficulty
    const matchesImpact = selectedImpact === 'All' || idea.impact === selectedImpact

    return matchesSearch && matchesCategory && matchesTime && matchesDifficulty && matchesImpact
  })

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'Beginner': return 'bg-green-100 text-green-800'
      case 'Intermediate': return 'bg-yellow-100 text-yellow-800'
      case 'Advanced': return 'bg-red-100 text-red-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const getImpactColor = (impact: string) => {
    switch (impact) {
      case 'High': return 'bg-blue-100 text-blue-800'
      case 'Medium': return 'bg-purple-100 text-purple-800'
      case 'Low': return 'bg-gray-100 text-gray-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        {/* Header */}
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-gray-900 mb-4">
            Extracurricular Idea Board
          </h1>
          <p className="text-xl text-gray-600 max-w-3xl mx-auto">
            Discover hands-on projects, volunteer opportunities, research internships, and community health initiatives 
            that will strengthen your college applications and deepen your healthcare knowledge.
          </p>
        </div>

        {/* Search and Filters */}
        <div className="bg-white rounded-lg shadow-sm p-6 mb-8">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4">
            <div className="lg:col-span-2">
              <Input
                placeholder="Search projects and opportunities..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full"
              />
            </div>
            <Select value={selectedCategory} onValueChange={setSelectedCategory}>
              <SelectTrigger>
                <SelectValue placeholder="Category" />
              </SelectTrigger>
              <SelectContent>
                {categories.map(category => (
                  <SelectItem key={category} value={category}>{category}</SelectItem>
                ))}
              </SelectContent>
            </Select>
            <Select value={selectedTime} onValueChange={setSelectedTime}>
              <SelectTrigger>
                <SelectValue placeholder="Time Commitment" />
              </SelectTrigger>
              <SelectContent>
                {timeCommitments.map(time => (
                  <SelectItem key={time} value={time}>{time}</SelectItem>
                ))}
              </SelectContent>
            </Select>
            <Select value={selectedDifficulty} onValueChange={setSelectedDifficulty}>
              <SelectTrigger>
                <SelectValue placeholder="Difficulty" />
              </SelectTrigger>
              <SelectContent>
                {difficulties.map(difficulty => (
                  <SelectItem key={difficulty} value={difficulty}>{difficulty}</SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        </div>

        {/* Results Count */}
        <div className="mb-6">
          <p className="text-gray-600">
            Showing {filteredIdeas.length} of {extracurricularIdeas.length} opportunities
          </p>
        </div>

        {/* Ideas Grid */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {filteredIdeas.map(idea => (
            <Card key={idea.id} className="hover:shadow-lg transition-shadow">
              <CardHeader>
                <div className="flex justify-between items-start">
                  <div className="flex-1">
                    <CardTitle className="text-xl font-semibold text-gray-900 mb-2">
                      {idea.title}
                    </CardTitle>
                    <CardDescription className="text-gray-600">
                      {idea.description}
                    </CardDescription>
                  </div>
                </div>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {/* Badges */}
                  <div className="flex flex-wrap gap-2">
                    <Badge variant="outline">{idea.category}</Badge>
                    <Badge variant="outline">{idea.timeCommitment}</Badge>
                    <Badge className={getDifficultyColor(idea.difficulty)}>
                      {idea.difficulty}
                    </Badge>
                    <Badge className={getImpactColor(idea.impact)}>
                      {idea.impact} Impact
                    </Badge>
                  </div>

                  {/* Healthcare Fields */}
                  <div>
                    <p className="text-sm font-medium text-gray-700 mb-2">Relevant Fields:</p>
                    <div className="flex flex-wrap gap-1">
                      {idea.fields.map(field => (
                        <Badge key={field} variant="secondary" className="text-xs">
                          {field}
                        </Badge>
                      ))}
                    </div>
                  </div>

                  {/* Skills */}
                  <div>
                    <p className="text-sm font-medium text-gray-700 mb-2">Skills Developed:</p>
                    <div className="flex flex-wrap gap-1">
                      {idea.skills.map(skill => (
                        <Badge key={skill} variant="outline" className="text-xs">
                          {skill}
                        </Badge>
                      ))}
                    </div>
                  </div>

                  {/* Stats and Actions */}
                  <div className="flex justify-between items-center pt-4 border-t">
                    <span className="text-sm text-gray-600">
                      {idea.participants} students interested
                    </span>
                    <div className="flex gap-2">
                      <Button variant="outline" size="sm">
                        Learn More
                      </Button>
                      <Button size="sm">
                        Get Started
                      </Button>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>

        {/* No Results */}
        {filteredIdeas.length === 0 && (
          <div className="text-center py-12">
            <h3 className="text-lg font-medium text-gray-900 mb-2">
              No opportunities found
            </h3>
            <p className="text-gray-600 mb-4">
              Try adjusting your search terms or filters
            </p>
            <Button onClick={() => {
              setSearchTerm('')
              setSelectedCategory('All')
              setSelectedTime('All')
              setSelectedDifficulty('All')
              setSelectedImpact('All')
            }}>
              Clear All Filters
            </Button>
          </div>
        )}

        {/* Submit Your Own Idea */}
        <div className="mt-16 bg-white rounded-lg shadow-lg p-8 text-center">
          <h2 className="text-2xl font-bold text-gray-900 mb-4">
            Have Your Own Project Idea?
          </h2>
          <p className="text-gray-600 mb-6">
            Share your extracurricular project with the community and get feedback from peers and mentors.
          </p>
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <Button size="lg">
              Submit Project Idea
            </Button>
            <Button variant="outline" size="lg">
              Browse Student Projects
            </Button>
          </div>
        </div>
      </div>
    </div>
  )
}
